.footer {
  padding: 10rem 3rem;
  background-color: #37383d;

  &__nav {
    list-style: none;
    display: flex;
    justify-content: center;
    margin-bottom: 5rem;
  }

  &__item {
    margin-right: 4rem;
  }

  &__link {
    font-size: 1.6rem;
    color: #eee;
    text-decoration: none;
  }

  &__logo {
    height: 5rem;
    display: block;
    margin: 0 auto;
    margin-bottom: 5rem;
  }

  &__copyright {
    font-size: 1.4rem;
    color: #aaa;
    text-align: center;

    .footer__link {
      font-size: 1.4rem;
    }
  }
}
